@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
#content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: white;
}




.positionAndDate #position {
    float: left;
    font-size: 28px;
    font-weight: bold;
}
.positionAndDate #date {
    float: right;
    font-size: 20px;
}
.companyAndLocation {
    clear:both;
}
.companyAndLocation #company{
    float: left;
    font-size: 25px;
}
.companyAndLocation #location{
    float: right;
    font-size: 20px
}
.icons {
    margin: 10px;
}

.heading {
    font-size: 30px;
    font-weight: bold;
}
ul li{
    font-size: 20px;
}
a {
  margin: 10px;
  display: inline-block;
}

a.linkedin {
    color: #0077B5;
}
a.github {
    color: black;
}
body {
    background-color: #ece6ff;
    padding: 20px;
    margin: 0;
    font-family: 'Mulish', sans-serif;
}
p {
  font-size: 20px;
}
  #title {
    display: flex;
    justify-content: center; 
    font-size: 50px;
    font-weight: bolder;
    width: 100%;
  }
  ul.header li {
    display: inline;
  }
  ul.header li a {
    font-weight: bold;
    text-decoration: none;
    padding: 30px;
    display: inline-flex;
    font-size: 30px;
    color: white;
  }
  ul.header li:hover {
    transform: scale(1.2, 1.2);
  }
  .header {
    padding: 0;
    background-color: #af97ff;
    display: flex;
    justify-content: center;
    width: auto;
  }
  .MenuContainer {
    float: right;
  }
  .content {
    padding: 20px;
  }
  .content h2 {
    padding: 0;
    margin: 0;
  }
  .content li {
    margin-bottom: 10px;
  }
  .active {
    background-color: #0099FF;
    border-radius: 25px;
    
  }
